  width: 1133px;
  height: 570px;
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 999;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-image: url(img/dialog.png?__sprite);
}
.dialog-close{
  width: 64px;
  height: 64px;
  background-image: url(img/close.png?__sprite);
  right: -12px;
  top: -12px;
  position: absolute;
}
.dialog header{
  width: 283px;
  height: 70px;
  color: #fff;
  font-size: 36px;
  background-image: url(img/header.png?__sprite);
  text-align: center;
  position: absolute;
  top: -16px;
  left: 50%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
  text-align: center;
  padding-top:11px;
}
.center-focus{
  background: url(img/center-focus.png?__sprite);
  width: 801px;
  height: 472px;
}
.center-focus ul{
  overflow: hidden;
  padding: 0 30px;
}
.center-focus li{
  float: left;
  width: 16.6%;
  height: 107px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.center-focus img{
  display: block;
}
.center-infor{
  padding: 13px 40px 0;
}
.center-infor li{
  color: #5e1f12;
