






























































.dialog{
  width: 1133px;
  height: 570px;
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 999;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-image: url(img/dialog.png?__sprite);
}
.dialog-close{
  width: 64px;
  height: 64px;
  background-image: url(img/close.png?__sprite);
  right: -12px;
  top: -12px;
  position: absolute;
}
.dialog header{
  width: 283px;
  height: 70px;
  color: #fff;
  font-size: 36px;
  background-image: url(img/header.png?__sprite);
  text-align: center;
  position: absolute;
  top: -16px;
  left: 50%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
  text-align: center;
  padding-top:11px;
}
.sence h1{
  text-indent: -999em;
  width: 150px;
  height: 41px;
  display: inline-block;
  background-image: url(img/title.png?__sprite);
}
.sence ul{
  overflow: hidden;
  padding-left: 63px;
  padding-top: 122px;
}
.sence li{
  float: left;
  margin-right: 7px;
  margin-bottom: 8px;
}



