































































.menu{
  position: absolute;
  right: 52px;
  top: 624px;
}
.menu ul{
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.menu li{
  text-indent: -999em;
  width: 94px;
  height: 89px;
  margin-left: 4px;
}
.menu li.active{
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
}
.menu li:nth-child(1){
  background-image: url(img/icon1.png?__sprite);
}
.menu li:nth-child(2){
  background-image: url(img/icon2.png?__sprite);
}
.menu li:nth-child(3){
  background-image: url(img/icon3.png?__sprite);
}
.menu li:nth-child(4){
  background-image: url(img/icon4.png?__sprite);
}



