  width: 1133px;
  height: 570px;
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 999;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-image: url(img/dialog.png?__sprite);
}
.dialog-close{
  width: 64px;
  height: 64px;
  background-image: url(img/close.png?__sprite);
  right: -12px;
  top: -12px;
  position: absolute;
}
.dialog header{
  width: 283px;
  height: 70px;
  color: #fff;
  font-size: 36px;
  background-image: url(img/header.png?__sprite);
  text-align: center;
  position: absolute;
  top: -16px;
  left: 50%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
  text-align: center;
  padding-top:11px;
}
.storehouse{
  background-image: url(img/bg.png?__sprite);
  width: 1193px;
  height: 584px;
}
.storehouse-title{
  text-indent: -999em;
  width: 99px;
  height: 42px;
  display: inline-block;
  background-image: url(img/title.png?__sprite);
}
.storehouse-tab li{
  width: 180px;
  height: 64px;
  background-image: url(img/li.png?__sprite);
  margin-bottom: 11px;
  font-size: 28px;
  color: #fff;
  text-align: center;
  padding-top: 16px;
  text-shadow: 0px 3px #b26d22,
         0px -3px #b26d22,
         3px 0px #b26d22,
         -3px 0px #b26d22;
  
}
.storehouse-tab li.active{
  background-image: url(img/li-active.png?__sprite);
